.login {
  padding: 16px;

  &_heading {
    font-size: 2.3rem;
  }

  &_card {
    margin-top: 4rem;

    &-heading {
      font-size: 1.3rem;
      margin-bottom: 10px;
    }

    &-subHeading {
      font-size: 1.3rem;
      margin-bottom: 10px;
    }

    &-form {
      margin: 40px 0;
    }

    &-input {
      background: var(--white-color);
      border-radius: 0px;
      box-shadow: none;
      border: none;
      padding: 0 8px;
    }

    &-footer {
      text-align: center;
    }
    &-resend{
      cursor: pointer;
      color: var(--cyan-color);
      margin-top: 8px;
      font-weight: bold;
    }
    &-countryCode{
      display: flex;
      background: var(--white-color);
      border-radius: 5px;
      padding: 10px;
      box-shadow: 0 8px 10px 7px rgba(201, 201, 201, 0.2);
      margin-bottom: 32px;
    &-select{
        border: none;
        height: 60px;
        background: var(--white-color);
        box-shadow:none;
        border-radius: 0px;
        font-size: 20px;
        color: var(--black-color);
        font-weight: 600;
        outline: none;
        width: 80px;
        display: inline-block;
        border: none;
        -webkit-appearance: none;
        background: var(--white-color) url(../../assets/down.png) no-repeat right 4px top 27px/14px;
      }
      //&-select::after {
      //  content: "";
      //  width: 30px;
      //  height: 30px;
      //  background:red url('../../assets/down.png') no-repeat -30px -50px fixed;
      //  top: 10px;
      //  right: 5px;
      //  position: absolute;
      //  display: inline-block;
      //}
    }
  }
}
.otpInput{
  width: 56px !important;
  padding: 0px;
  color: var(--grey-color);
}
.optFocusInput{
  border-color: var(--cyan-color) ;
}
.otpContainer{
  margin-bottom: 32px;
  justify-content: center;
}