button{
  --webkit-apperance:none;
}

.btn{
  border: 0;
  padding:1rem 4rem;
  text-transform: uppercase;
  border-radius: 20rem;
  // float: right;
  letter-spacing: 1.2px;
  // width: 100%;
  font-size: 1.3rem;

  @include respond(tab-land){
    font-size: 1.1rem;
  }
  @include respond(tab-port){
    font-size: 1.5rem;
    width: 100%;
  }
  @include respond(phone){
    font-size: 1rem;
  }

  &:link,&:visited{

  }
  &:hover{
    cursor: pointer;
  }
  &:active{
    background-color: darken($prep-blue-color,10%);
  }
  &:focus{
    outline: none;
  }

  &-txt-color-white{
    color: white;
  }

  &-blue-fill{
    background-color: $prep-blue-color;
    border: 1px solid $prep-blue-color;
  }
  &-txt-color-blue{
    color:$prep-blue-color;
  }
  &-white-fill{
    background-color: white;
    border: 1px solid $prep-blue-color;
  }

}

.btn-1{
  border: 0;
  padding:.7rem 3rem;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  width: 100%;
  text-decoration: none;
  @include respond(tab-land){
    font-size: 1.1rem;
  }
  @include respond(tab-port){
    font-size: 1.5rem;
  }
  @include respond(phone){
    font-size: 1rem;
  }


  &:hover{
    cursor: pointer;
  }
  &:focus{
    outline: none;
  }
  &:active{
    background-color: $grey-color-light;
  }
  &-color-blue{
    color: $prep-blue-color;
  }
  &-border-blue{
    background-color: white;
    border: 1.3px solid $prep-blue-color;
  }
}

.btn-2{
  // border: 1px solid var(--prep-green-color);
  padding: .5rem 1rem;
  text-transform: uppercase;
  // color: var(--prep-green-color);
  font-size: $default-font-size;
  background-color: var(--white-color);
  text-decoration: none;

  &:focus{
    outline: none;

  }
  &:active{
    background-color: rgba(0,0,0,.1);
  }
  &:hover{
    cursor: pointer;
  }
}

.fill-btn-sqr{
  color: var(--white-color);
  padding: .9rem 4rem;
  border: 0;
  text-transform: uppercase;
  font-size: 1.3rem;
  text-decoration: none;
  &:focus{
    outline: none;

  }
  &:active{
    transform: scale(.98);
    background-color: rgba(0,0,0,.4);
  }
  &:hover{
    cursor: pointer;
  }
}

.btn-main{
  padding: 0 1.5rem;
  cursor: pointer;
  font-size: 1.6rem;
  box-shadow: none;
  background-color: transparent;
  -webkit-appearance: none;
  font-family: inherit;
  border-radius: 4px;
  &:not(:last-child){
    margin-right: .8rem;
  }
  &:focus{
    outline: none;
  }
}


.btn-blue, .btn-red{
  color: #fff;
}

.btn-blue{
  background: var(--prep-blue-color);
}

.btn-red{
  background: var(--red-color);
}
 input[type="submit"]{
  padding: 16px 40px;
  display: inline-block;
  cursor: pointer;
  font-weight: 600;
  border: none;
  font-size: 20px;
  transition-duration: 0.3s;
  border-radius: 5px;
  background: var(--cyan-color);
  color: #fff;
  text-transform: uppercase;
}


input {
  background: var(--white);
  height: 60px;
  box-shadow: 0 0 8px 2px #F0F0F0;
  width: 100%;
  padding: 15px 20px 10px;
  border-radius: 6px;
  font-size: 20px;
  color: var(--grey-color);
  background: var(--white);
  border: 1px solid var(--border);
  font-weight: 500;

  &::placeholder {
    color: #9CA3B9;
  }
}

.select{
  position: relative;
  &_input{
    cursor: pointer;
    outline: none;
    background: var(--white);
    height: 60px;
    box-shadow: 0 0 8px 2px #F0F0F0;
    border: none;
    width: 100%;
    padding: 15px 48px 15px 20px;
    border-radius: 6px;
    font-size: 20px;
    margin-bottom: 16px;
    color: var(--grey-color);

    &::placeholder {
      color: #9CA3B9;
    }
    &-arrow{
      cursor: pointer;
      position: absolute;
      background-image: url("../../assets/down.png");
      background-size: contain;
      top: 24px;
      right: 11px;
      background-repeat: no-repeat;
      width: 20px;
      height: 20px;
    }
  }
}

.invalid {
  border-color: var(--red-color) !important;
}

