*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}


:root{
  --cyan-color:#0DD9B3;
  --sidebar-width: #{$sidebar-width};
  --grey-color-bg:#FAFAFA;
  --grey-color:#8D8C8C;
  --grey-color-light:#f1f1f1;
  --grey-color-light-1:#cbcbcb;
  --grey-color-light-2:#e3e3e3;
  --grey-color-dark:#878787;
  --grey-color-dark-1:#737373;
  --heading-color:#474747;
  --subheading-color:#9c9c9c;

  --prep-blue-color:#26b3f9;
  --prep-green-color:#17cd1f;
  --green-color-2:#2fc54d;;

  --orange-color:#ff6d00;
  --orange-color-light:#ff9c06;

  --red-color:#ff0000;
  --brown-color:#7b320a;
  --pink-color:#f9267a;
  --purple-color:#b118b7;

  --black-color:#000;
  --white-color:#fff;

  --default: #6a7695;
  --heading: #263b5e;
  --theme: #0dd9b3;
  --theme-light: #dbf7f1;
  --light: #f9f7fd;
  --light-1: #fbfbfd;
  --green: #3aac55;
  --red: #ff4d4d;
  --border: #e9e5f3;
}


html{
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}

body{
  box-sizing: border-box;
  font-size: 16px;
  color: var(--grey-color);
  background-color: var(--white);
  font-family: 'Roboto', sans-serif;
  font-weight: 500;


}





::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #C1C1C1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--light-gray);

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--light-gray);
}


