//grid
$max-width:192rem;
$gutter-vertical: 6rem;
$gutter-vertical-sm: 3rem;
$gutter-vertical-xm: 2rem;

$gutter-horizontal: 3rem;
$gutter-horizontal-lg: 13rem;


//main-page width
$main-page-width:85%;


//footer height
$bottom-height:6rem;


//font
$default-font-size:1.6rem;
$paragraph-font:1.5rem;

//sidebar width
$sidebar-width:18rem;

//header height
$header-height:60px;


//colors

$grey-color-topbar:#cbcbcb;
$grey-color-dark:#878787;
$grey-color-dark-1:#737373;
$grey-color-light:#f1f1f1;


$heading-color:#474747;
$prep-blue-color:#26b3f9;
$black-color:#000;
$subheading-color:#9c9c9c;



$datepicker__background-color: #f0f0f0 !default;
$datepicker__border-color: #aeaeae !default;
$datepicker__highlighted-color: #3dcc4a !default;
$datepicker__muted-color: #ccc !default;
$datepicker__selected-color: #26b3f9 !default;
$datepicker__text-color: var(--heading-color) !default;
$datepicker__header-color: var(--heading-color) !default;
$datepicker__navigation-disabled-color: lighten($datepicker__muted-color, 10%) !default;

$datepicker__border-radius: 0.3rem !default;
$datepicker__day-margin: 0.166rem !default;
$datepicker__font-size: 1.3rem !default;
$datepicker__font-family: "Helvetica Neue", Helvetica, Arial,sans-serif !default;
$datepicker__item-size: 1.7rem !default;
$datepicker__margin: 0.4rem !default;
$datepicker__navigation-size: 0.45rem !default;
$datepicker__triangle-size: 8px !default;

